import clear from "./clear.js";

const del = (screen) => {
  const expression = screen.lScreen.textContent;
  screen.hScreen.textContent = "";
  screen.hScreen.innerHTML = "";

  if (screen.displayError == true) {
    clear(screen);
  } else {
    let charDelete = expression.slice(-1) == " " ? -3 : -1;
    charDelete =
      expression.trim().endsWith("cos(") ||
      expression.trim().endsWith("sin(") ||
      expression.trim().endsWith("tan(") ||
      expression.trim().endsWith("log(")
        ? -5
        : charDelete;
    screen.lScreen.textContent = expression.slice(0, charDelete);
  }
};

export default del;
